$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    console.log(parentBlock)
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

$("[data-copy]").click(function () {
    let text = $(this).attr("data-copy")
    let $tmp = $("<textarea>");
    $("body").append($tmp);
    $tmp.val(text).select();
    document.execCommand("copy");
    $tmp.remove();
    $(".copy-alert").remove()

    $(".m-page").append(`
    <div class="copy-alert _active">
        <div class="copy-alert__title">
            <div class="text16 medium-text">
                Copied
            </div>
        </div>
    </div>
    `)

    setTimeout(()=>{
        $(".copy-alert").addClass("_active")
    },10)

    setTimeout(()=>{
        closeAlert(".copy-alert")
    },10000)
})

function closeAlert(el) {
    $(el).removeClass("_active")
    setTimeout(()=>{
        $(el).remove()
    },300)
}

$(".qa-card__show").click(function () {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})

$("._show-token-options").click(function () {
    $(".select-token").addClass("_active")
    $("body").addClass("fixed-body")
})

$(".token-option").click(function () {
    let title = $(this).attr("data-title")
    let img = $(this).attr("data-img")

    $(".token-field__title").html(title)
    $(".token-field__icon").attr("src",img)

    $(".token-option").removeClass("_active")
    $(this).addClass("_active")

    $(".select-token").removeClass("_active")
    $("body").removeClass("fixed-body")

    console.log(title)
    console.log(img)
})

