function mainSlider() {
    var swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        effect: "fade",
        allowTouchMove: false,
        autoplay: {
            pauseOnMouseEnter: false,
            disableOnInteraction: false,
            delay: 4000,
        }
    })
}

mainSlider()
